body {
     margin: 0px;
     font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
     'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
     sans-serif;
     -webkit-font-smoothing: antialiased;
     -moz-osx-font-smoothing: grayscale;
}
p{
     margin:0px;
}
code {
     font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
     monospace;
}


.App {
     text-align: center;
}

.App-logo {
     height: 40vmin;
     pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
     .App-logo {
          animation: App-logo-spin infinite 20s linear;
     }
}

.App-header {
     background-color: #282c34;
     min-height: 100vh;
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
     font-size: calc(10px + 2vmin);
     color: white;
}

.App-link {
     color: #61dafb;
}

@keyframes App-logo-spin {
     from {
          transform: rotate(0deg);
     }
     to {
          transform: rotate(360deg);
     }
}



:root{

     --f7-block-header-margin: 3px;
     --f7-block-padding-vertical: 3px;
     --f7-block-margin-vertical: 3px;
     --f7-list-margin-vertical: 3px;

     --f7-card-content-padding-horizontal: 0px;
     --f7-card-content-padding-vertical: 0px;
}

.ios{

     --f7-card-margin-horizontal: 0px;
     --f7-card-margin-vertical: 0px;
}
.md{

     --f7-card-margin-horizontal: 0px;
     --f7-card-margin-vertical: 0px;
}

.sheet-modal{
     height:var(--none);
}

.infinite-scroll-preloader {
     margin-top:-20px;
     margin-bottom: 10px;
     text-align: center;
}
.infinite-scroll-preloader .preloader {
     width:34px;
     height:34px;
}


.stepper-fill,
.ios .stepper-fill-ios,
.md .stepper-fill-md,
.aurora .stepper-fill-aurora {
     --f7-stepper-button-bg-color:#ff0000;
     --f7-stepper-button-text-color: #00ff00;
     --f7-touch-ripple-color: #0000ff;
     --f7-card-expandable-margin-horizontal: 0px;
     --f7-card-expandable-margin-vertical: 0px;
}

:root {
     --f7-grid-gap: 0.5px;
     --f7-grid-row-gap: 1px;
     --f7-grid-resize-handler-bg-color: rgba(0, 0, 0, 0.35);
}

full-width-img
{
     width: 100%;
     height: auto;
     top:50%;
     -webkit-transform: translateY(-50%);
     -ms-transform: translateY(-50%);
     -moz-transform: translateY(-50%);
     position: relative;
}
full-height-img
{
     width: auto;
     height: 100%;
     top:50%;
     -webkit-transform: translateX(-50%);
     -ms-transform: translateX(-50%);
     -moz-transform: translateX(-50%);
     position: relative;
}
.contentDiv{
     display:inline-block;
     margin:5px auto;
     width:100px;
     height:100px;
     border-radius:100px;
     -webkit-border-radius:100px;
     -moz-border-radius:100px;
     border:2px solid #fff;
     box-shadow:0 0 4px #ccc;
     overflow:hidden;
}


.commentAvatarImage{    width:32px;    height:32px;    line-height: 0;		/* remove line-height */     display: inline-block;	/* circle wraps image */   border-radius: 50%;	/* relative value */   -moz-border-radius: 50%;   -webkit-border-radius: 50%;   transition: linear 0.25s;}
.commentAvatarImage2{    width:80px;    height:80px;    line-height: 0;		/* remove line-height */     display: inline-block;	/* circle wraps image */   border-radius: 50%;	/* relative value */   -moz-border-radius: 50%;   -webkit-border-radius: 50%;   transition: linear 0.25s;}

.sound a{
     width:35px;
     height:35px;
}

img.lazy{
     background: whitesmoke;
}
.page-content{

     overflow-x: hidden;
     /*overflow-y: scroll;*/
}

.stacked,.page-previous{
     left:10000px;
}
.page-current{
     left:0px;
}


.view-text{
     /**
       思路：
       1.设置inline-block属相
       2.强制不换行
       3.固定高度
       4.隐藏超出部分
       5.显示“……”
     */
     display: inline-block;
     white-space: nowrap;
     width: 100%;
     overflow: hidden;
     text-overflow:ellipsis;
}


.text-unselectable {
     -webkit-user-select: none;
     -moz-user-select: none;
     -o-user-select: none;
     user-select: none;
     /*background: #eee;*/
}


.author-face-container{

     min-height:160px;

     height:auto !important;

     height:160px;

}
.list input[type=datetime-local] {
     max-width: 80vw;
}