:root{
    --goodsListItemImgWidth:30vw;
    --goodsListItemImgHeight:30vw;
}

.platformNameLine{
    font-size:0.8rem;
    color:#505050;
}
.couponLine{
    font-size:0.7rem;
    color: #fd3a3a;
}
.goodsNameLine{
    font-size:0.9rem;
    font-weight: bold;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    /*box-orient: vertical;*/
    -webkit-line-clamp: 2;
    /*line-clamp: 2;*/
    overflow: hidden;
}
.floatLeft{
    float:left;
}
.redText{
    color:#ff0000;
}

.img{
    width:100%;
}
.imgContainer{
    /*border: solid;color: red;*/
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    width: var(--goodsListItemImgWidth);
    height:var(--goodsListItemImgHeight);
}

.wrapper {
    display: flex;
    flex-direction: row;
    gap:0.5rem;
}

.left {
    /*background-color: red;*/
    width: var(--goodsListItemImgWidth);
}
.middle {
    /*background-color: green;*/
    flex: 1;
}
.right {
    background-color: blue;
    width: 100px;
}

.rightContainer{
    display:flex;
    flex-direction:column;
    gap:0.2rem;
}

.rightContainerFlex{
    flex:1;
}